var self;
export default {
	data() {
		return {
			baseurl: "http://39.105.48.243:8080/crlink/",
			selectIndex: 1,
			collegeArr: [],
			couserArr: []
		};
	},

	onLoad() {
		self = this;
		self.getData()
	},

	methods: {
		
		
		getData(){
			let loginkey = uni.getStorageSync('loginKey')
			this.$api.post('myCourse!ajaxGetColectCourse.action',{
				loginKey:loginkey
			}).then(res=>{
				if(res.res.status ==0){
					self.collegeArr =  res.inf.collegeArr
					self.couserArr = res.inf.courseArr

				}
			})
		},

		selectItem(e) {
			if (self.selectIndex !== e) {
				self.selectIndex = e;

			}
		},
			
		itemClick(item){
				
			if(item.flag == "course"){
				uni.navigateTo({ 
					url:'../../home/videoDetail?courseID='+item.id
				})
			}else{
				uni.navigateTo({ 
					url:'../../home/college?collegeId='+item.id
				
				})
			}
		}
	}
}
